
import { ref } from 'vue';
import { useRouter } from 'vue-router';
export default {
  name: 'ListBox',
  props: {
    title: {
      type: String,
      default: '',
    },
    list: {
      type: Array,
    },
    type: {
      type: String,
    },
  },
  setup() {
    const show = ref(false);
    const handleClickShow = () => {
      show.value = !show.value;
    };
    const router = useRouter();
    const go = (link) => {
      if (link) {
        router.push(link);
      }
    };
    return {
      show,
      handleClickShow,
      go,
    };
  },
};
