
import { useRouter } from 'vue-router'
import { reactive } from 'vue'
import { components } from './components'
export default {
  setup() {
    const router = useRouter()
    const handleClick = () => {
      router.push('/button')
    }
    const list = reactive(components)
    return {
      handleClick,
      list,
    }
  },
}
