
import { useRouter } from 'vue-router'

export default {
  name: 'Page',
  props: {
    title: {
      type: String,
      default: '',
      required: true,
    },
    type: {
      type: String,
      default: '',
    },
    desc: {
      type: String,
      default: '',
    },
  },
  setup() {
    const router = useRouter()
    const back = () => {
      router.back()
    }
    return {
      back,
    }
  },
}
